@use "../../foundation/variables" as *;

// お悩み
.p-troubles {
  overflow: hidden;
  background-color: #ffffff;
  &__circle {
    position: relative;
    margin: 150px auto 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 800px;
      box-sizing: border-box;
      aspect-ratio: 1/1;
      border-radius: 50%;
      border: 1px solid #ccc;
      transform: translate(-50%, -50%);
      box-shadow: 0px 0px 10px #83838360;
    }
  }

  &__left,
  &__right {
    z-index: 2;
    max-width: 380px;
    width: 100%;
  }

  &__left {
    position: relative;
    left: -30px;
    text-align: right;

    .p-troubles__item:nth-of-type(2),
    .p-troubles__item:nth-of-type(3) {
      left: -80px;
    }

    .p-troubles__item:nth-of-type(1) {
      &:before {
        bottom: -30px;
        right: 5%;
      }

      &:after {
        bottom: -45px;
        right: 2%;
      }
    }

    .p-troubles__item:nth-of-type(2) {
      &:before {
        bottom: 10%;
        right: -8%;
      }

      &:after {
        bottom: 6%;
        right: -13%;
      }
    }

    .p-troubles__item:nth-of-type(3) {
      &:before {
        bottom: 25%;
        right: -8%;
      }

      &:after {
        bottom: 40%;
        right: -13%;
      }
    }

    .p-troubles__item:nth-of-type(4) {
      &:before {
        top: -37%;
        right: -3%;
      }

      &:after {
        top: -56%;
        right: -6%;
      }
    }
  }

  &__right {
    text-align: left;
    position: relative;
    right: -30px;

    .p-troubles__item:nth-of-type(2),
    .p-troubles__item:nth-of-type(3) {
      right: -80px;
    }

    .p-troubles__item:nth-of-type(1) {
      &:before {
        bottom: -30px;
        left: 5%;
      }

      &:after {
        bottom: -45px;
        left: 2%;
      }
    }

    .p-troubles__item:nth-of-type(2) {
      &:before {
        bottom: 10%;
        left: -8%;
      }

      &:after {
        bottom: 6%;
        left: -13%;
      }
    }

    .p-troubles__item:nth-of-type(3) {
      &:before {
        bottom: 25%;
        left: -8%;
      }

      &:after {
        bottom: 40%;
        left: -13%;
      }
    }

    .p-troubles__item:nth-of-type(4) {
      &:before {
        top: -37%;
        left: -3%;
      }

      &:after {
        top: -56%;
        left: -6%;
      }
    }
  }

  &__center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 350px;
    background-color: #fff;

    img {
      display: block;
    }
  }

  &__item {
    max-width: 380px;
    width: 100%;
    box-sizing: border-box;
    background-color: #ffffff;
    border-radius: 25px;
    font-size: 1.8rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    padding: 15px 20px;
    z-index: 2;
    margin: 60px 0;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      margin-right: 0;
      display: block;
      width: 20px;
      height: 20px;
      background: #ffffff;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
      border-radius: 50%;
      z-index: 0;
    }

    &:after {
      content: "";
      position: absolute;
      margin-right: 0;
      display: block;
      width: 12px;
      height: 12px;
      background: #ffffff;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
      border-radius: 50%;
      z-index: 0;
    }
  }
}
